body{
    margin: 0px;
    padding: 0px;
    background-color:#161616 ;
}
ul{
    list-style: none;
}
a{
    text-decoration: none;
}
#main{
    width: 100%;
    height: 100vh;
    background-color: #edf1fd;
    position: relative;
    background-image:url("bg2.jpg");
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: 1400px;
}
nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 2px;
    font-family: Calibri;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    box-sizing: border-box;
    padding: 10px 50px;
    background-color: #f4f7ff;
    box-shadow: 2px 2px 12px rgba(0,0,0,0.05);
    z-index: 1;
}
.menu{
    display: flex;
}
.menu li a{
    padding: 10px 15px;
    color: #6c707c;
}
.logo{
    font-size: 25px;
    font-weight: bold;
    color: #292929;
    font-family: Poppins;
}
.toggle{
    display: none;
}
.menu li a:hover,
.active{
    background-color: #292929;
    color: #FFFFFF !important;
    font-weight: 700;
    transition: all ease 0.4s;
}
.name{
    font-family: Calibri;
    width: 500px;
    position: absolute;
    left: 20%;
    top:50%;
    transform: translate(-20%,-50%);
}
.name p:nth-child(1),
.name .details{
    color: #6c707c;
    font-size: 16px;

}
.name h1{
    font-family: Poppins;
    font-size: 40px;
    margin: 0px;
    letter-spacing: 3px;
    color: #292929;

}
.cv-btn{
    width: 200px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #292929;
    color: #FFFFFF;
}
.cv-btn:hover{
    background-color: #131313;
    transition: all ease 0.5s;
}
.social{
    position: absolute;
    left: 50px;
    bottom:50px;
    display: flex;
}
.social a{
    margin: 6px 12px;

}
.social i{
    color: rgba(18,17,17,0.4);
    font-size: 14px;
}
.social a:hover i{
    color: #17d1ac;
    transition: all ease 0.5s;
}
.arrow{
    align-self: end;
    width: 50%;
    height: 15%;
    margin-bottom: 4em;
    position: absolute;
    bottom: 0px;
    left: -3%;
    border-right: 1px solid rgba(41 41 41 0.3);
}
.arrow::after{
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 11px 11px 0px 11px;
    border-color: #292929 transparent transparent transparent;
    right: -0.7em;
    bottom: -2px;
}
@media (max-width: 1190px) {
    #main{
        background-size: 1050px;
    }
    .name{
        left: 10%;
        top: 50%;
        transform: translate(-10%,-50%);
    }

}
@media (max-width: 970px) {
    #main{
        background-image: none !important;
    }
    .name{
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
}
@media (max-width: 900px) {
    .toggle {
        display: block;
    }
    .toggle :before{
        line-height: 0px;
        margin: 25px;
    }
    nav{
        padding:10px 30px;
    }

    nav ul{
        position:absolute;
        width: 100%;
        height:auto;
        box-sizing:border-box;
        background-color:#0F0F0F;
        top: 50px;
        left: 0;
        transition: 0.5s;
        overflow: hidden;
        display: none !important;
        border:3px solid #1F1F1F;
    }

    nav ul li a{
        border-bottom: 1px solid rgba(255,255,255,0.10) ;
        color:#ffffff !important;
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0px !important;
        padding: 0px !important;
    }
    nav ul{
        padding:0px;
        margin:0px;
    }

    .active-menu {
        display: block !important;
    }

    nav ul li a:hover{
        background-color:rgba(27,29,32,0.15);

    }
}