#services{
    width: 100%;
    height: 100vh;
    font-family: Calibri;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 40px;
}
.s-heading h1{
    color: #FFFFFF;
    font-size: 3rem;
    margin: 0px;
    padding: 0px;
}
.s-heading p{
    color: rgba(249,249,249,0.30);
    font-size: 1rem;
    padding: 0px;

}
.s-heading{
    text-align: center;
    margin: 20px 0px;
}
s-box{
    background-color: #2b2b2b;
    width: 350px;
    height: 500px;
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 2px 2px 12px rgba(0,0,0,0.1);
    position: relative;
}
.s-b-container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.s-b-img{
    width: 100%;
    height: 70%;
    position: relative;

}
.s-b-img img{
    width: 97%;
    height: 100%;
    object-fit: cover;
}
.s-b-text{
    width: 100%;
    height: 30%;
    background-color: #1e1e1e;
    display: flex;
    justify-content: center;
    align-items: center;

}
.s-b-text a{
    margin: 0px;
    color: #FFFFFF;
    font-size: 1.1rem;
    font-family: Calibri;
    display: -webkit-box;
    max-width: 80%;
    -webkit-line-clamp:4;
    overflow: hidden;
    text-overflow:ellipsis;
    -webkit-box-orient: vertical;
}
.s-type{
    font-family: Calibri;
    color: #FFFFFF;
    background-color: #151515;
    padding: 10px 15px;
    position: absolute;
    top:0;
    left: 50%;
    transform: translateX(-50%);
    font-size: 18px;
    border-radius: 0px 0px 10px 10px;
    box-shadow: 2px 2px 12px rgba(0,0,0,0.2);
}
.s-box:hover{
    transform: translateY(-10px);
    transition: all ease 0.3s;
}
.s-b-text a:hover{
    color: rgba(155,155,255,0.50);
}
@media (max-width: 1190px) {
    #services{
        height: auto;
    }
    .s-b-container{
        flex-wrap: wrap;
    }
    .s-box{
        flex-grow: 1;
    }
    .s-b-text{
        text-align: center;
    }
}
@media(max-width:400px){
    .s-box{
        width: 100%;
        height:400px;
    }
}